import {Link} from "react-router-dom"
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';
import axios from "axios"
import React, { useState, useEffect, Component } from "react";
import { useHistory } from "react-router-dom";
import { makeStyles } from "@material-ui/core/styles";
import Button from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Modal from '@material-ui/core/Modal';

function Home(props) {
  const [coins, setCoins] = useState([])
  const history = useHistory();

  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  useEffect(() => {
    document.body.style = 'background: linear-gradient(90deg, rgb(46, 186, 198) 0%, rgb(182, 80, 158) 100%);';
    fetchCoinData()
  }, [])

  function getModalStyle() {
    const top = 50
    const left = 50

    return {
      top: `${top}%`,
      left: `${left}%`,
      transform: `translate(-${top}%, -${left}%)`,
    };
  }

  const useStyles = makeStyles(theme => ({
  inputRoot: {
    color: "white",
    "& .MuiOutlinedInput-notchedOutline": {
      borderColor: "white"
    },
    "&:hover .MuiOutlinedInput-notchedOutline": {
      borderColor: "white"
    },
    "&.Mui-focused .MuiOutlinedInput-notchedOutline": {
      borderColor: "white"
    },
    "&.MuiOutlinedInput-input":{
      color: "white"
    }
  },
  paper: {
    position: 'absolute',
    width: 600,
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));
const classes = useStyles();

 const [modalStyle] = React.useState(getModalStyle);
 const [open, setOpen] = React.useState(false);

 const handleOpenModal = () => {
   setOpen(true);
 };

 const handleCloseModal = () => {
   setOpen(false);
 };

 const body = (
    <div style={modalStyle} className={classes.paper}>
      <h2 id="simple-modal-title">Tip the dev :)</h2>
      <p id="simple-modal-description">
        BTC: 1Mqk3gqp9agLV4AtarxzA19GycAuJquWvG
        ETH: 0x466E7D500ECFE3190E11F61F9c2aa1BD4790c552
      </p>
    </div>
  );

  return (
    <>
    <Modal
        open={open}
        onClose={handleCloseModal}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
      >
        {body}
      </Modal>

    <Button className="ml-4 mt-4" variant="contained" color="secondary" aria-controls="simple-menu" aria-haspopup="true" onClick={handleClick}>
        FutureCap
      </Button>
      <Menu
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <MenuItem onClick={() => window.location.href = 'https://www.youtube.com/channel/UCSI8zjZx3Y-dqCvShs43nyg' }>My channel (Eager Money)</MenuItem>
        <MenuItem onClick={() => {handleOpenModal(); handleClose()}}>Tip</MenuItem>
      </Menu>

      <div class="home container text-center" style={{color: "white", marginTop: "120px"}}>
      <span style={{fontSize: "6rem", verticalAlign: "middle", fontWeight: "800"}} className="ml-4 coin-name">FutureCap</span>

      <Autocomplete

        id="combo-box-demo"
        classes={classes}
        options={coins}
        getOptionLabel={(coins) => `${coins.name} (${coins.symbol.toUpperCase()})`}
        onChange={(event, value) => history.push("/coin/" + value.id)}
        style={{ width: "100%", color: "white"}}
        color="secondary"

        renderInput={(params) => <TextField {...params} label="Search for a coin" variant="outlined" />}
      />
      </div>

    </>
  );

  function fetchCoinData(){
    console.log("fetching")
    axios.get("/coins")
    .then(res => {
      setCoins(res.data.coins)
    })
    .catch(error => {
    })
  }
}

export default Home;
