import {Link} from "react-router-dom"
import TextField from '@material-ui/core/TextField';
import axios from "axios"
import React, { useState, useEffect, Component, useRef } from "react";
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';
import LinearProgress from '@material-ui/core/LinearProgress';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import { useHistory } from "react-router-dom";

function Coin(props) {
  const history = useHistory();

  const [coin, setCoin] = useState(null)
  const [buttonClicked, setButtonClicked] = useState(false)
  const [isLoading, setIsLoaing] = useState(true)
  const [phraseIndex, setPhraseIndex] = useState(0)


  useEffect(() => {
    document.body.style = 'background: #383D51;';

    if (!coin){
      fetchCoinData()
    }else{
      if (phraseIndex >= coin.phrases.length){
        setIsLoaing(false)
      }
    }
  }, [phraseIndex])

  function incrementPhaseIndex(){
    if (phraseIndex < coin.phrases.length - 1){
      setPhraseIndex(index => index + 1);
      setTimeout(incrementPhaseIndex, 1000)
    }
  }

  const useStyles = makeStyles((theme) => ({
    root: {
      width: '40%',
      background: "linear-gradient(90deg, rgb(46, 186, 198) 0%, rgb(182, 80, 158) 100%)"},
      justify: "center",
    button: {
      margin: theme.spacing(1),
    },
  }));

  const classes = useStyles();
  return (
    <>
      <Button
          variant="contained"
          color="secondary"
          className={`${classes.button} ml-4 mt-4`}
          onClick={() => history.push("/")}
        >
        Home
      </Button>
      <div style={{color: "white", marginTop: "120px"}} class="coin container text-center">
          <div>
            <img style={{verticalAlign: "middle", height: "180px"}} src={coin ? coin.image : ""} />
            <span style={{fontSize: "6rem", verticalAlign: "middle", fontWeight: "800"}} className="ml-4 coin-name">{coin ? coin.name : ""}</span>
          </div>

        <div className="price mt-4">
          {coin ?
            <h1 className="text-center" style={{fontSize: "3.5rem", textAlign: "right"}} >{`$${coin.price}`} <span style={{verticalAlign: "middle", fontSize: "1.5rem", color: (coin.change < 0 ? "red" : "green")}}>{`${coin.change > 0 ? "+" : ""}${coin.change}%`}</span></h1>
          : null
          }
        </div>

        <div className="text-center" style={{marginTop: "100px"}}>
          {
            buttonClicked ?
            <div>
              {isLoading ?
                <div>
                  <LinearProgress color="secondary" />
                  <h5 className="mt-3" style={{fontWeight: "300"}}>{coin ? coin.phrases[phraseIndex] : ""}</h5>
                </div>
                :
                <div >
                  <List className={classes.root} style={{left: "30%", border: "5px solid white", borderRadius: "5px" }}>
                    <ListItem>
                      <ListItemText primary="Lower target" secondary={`$${coin.lower}`} />
                    </ListItem>
                    <ListItem>
                      <ListItemText primary="Medium target" secondary={`$${coin.middle}`} />
                    </ListItem>
                    <ListItem>
                      <ListItemText primary="Upper target" secondary={`$${coin.upper}`} />
                    </ListItem>
                  </List>


                </div>
              }
            </div>
            :
            <Button
              variant="contained"
              className={classes.button}
              onClick={() => {
                setButtonClicked(true)
                setTimeout(incrementPhaseIndex, 1500)
              }}
              style={{background: "linear-gradient(90deg, rgb(46, 186, 198) 0%, rgb(182, 80, 158) 100%)", width: "300px", height: "50px", color: "white", fontWeight: "400"}}>
              Generate 2021 price targets
            </Button>
          }
        </div>
      </div>
    </>
  );

  function fetchCoinData(){
    axios.get("/coins/" + props.match.params.id)
    .then(res => {
      setCoin(res.data.coin)
      console.log(res.data.coin)
    })
    .catch(error => {
    })
  }
}

function useInterval(callback, delay) {
  const savedCallback = useRef();

  // Remember the latest callback.
  useEffect(() => {
    savedCallback.current = callback;
  }, [callback]);

  // Set up the interval.
  useEffect(() => {
    let id = setInterval(() => {
      savedCallback.current();
    }, delay);
    return () => clearInterval(id);
  }, [delay]);
}
export default Coin;
